<template>
	<view class="container">
		<view class="info" v-if="status==1">
			<image src="../../static/icon/success.png" mode="" class="icon"></image>
			<view class="text">支付成功</view>
		</view>
		<view class="info" v-else>
			<image src="../../static/icon/fail.png" mode="" class="icon"></image>
			<view class="text">支付失败</view>
		</view>
		<view class="btn-box">
			<button class="btn btn1" @click="toOrder">查看订单</button>
			<button class="btn btn2" @click="toIndex">返回首页</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				status: '',
			}
		},
		onLoad(options) {
			this.status = options.status
		},
		methods: {
			toOrder() {
				if (this.status == 1) {
					uni.navigateTo({
						url: `/pages/order/order?status=2`
					})
				} else {
					uni.navigateTo({
						url: `/pages/order/order?status=1`
					})
				}
			},
			toIndex() {
				uni.switchTab({
					url: '/pages/index/index'
				})
			}
		}
	}
</script>

<style lang="scss">
	.container {
		padding: 200upx 50upx 0;

		.info {
			text-align: center;

			.icon {
				width: 120upx;
				height: 120upx;
			}

			.text {
				font-size: 30upx;
				margin-top: 10upx;
			}
		}

		.btn-box {
			margin-top: 120upx;

			.btn1 {
				background-color: #009000;
				color: #fff;
			}

			.btn2 {
				background-color: #fff;
				border: 1px solid #cacaca;
				margin-top: 30upx;
			}
		}
	}
</style>